.profileTop {
    display: flex;
    justify-content: flex-start;

}

.photoItem {
    width: 200px;
    height: 300px;
    min-width: 250px;
    min-height: 300px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #FFFFFF;
    border-radius: 2%;
    box-shadow: 0 2px 2px 2px #192124;


}

.photo {
    width: 90%;
    height: 90%;
    border-radius: 2%;

}

.info {
    margin-left: 1em;
    background-color: #FFFFFF;
    width: 100%;
    height: 300px;
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
    border-radius: 1%;
    box-shadow: 2px 2px 2px 2px #192124;



}
.name {
    font-size: 30px;


}

.line {
    border-bottom: 1px solid #192124;
    width: 100%;
}

.userData {
    padding: 10px;
}
.userInfo {
    display: flex;
    justify-content: space-between;
    padding: 10px 15px;
    background-color: #192124;

}
.userInfo ul {
    padding: 0;
    margin: 0;
    list-style-type: none;
    width: 40%;

}

.nameWrapper > * {
    padding-left: 15px;
    margin: 0;
}

.nameWrapper > :last-child {
    color: #192124;
    font-size: 15px;
}