.wrapper {
    display: grid;
    grid-template-areas: 'img name loc'
                         'btn stat loc';
    grid-template-columns: 120px 2fr 2fr;

    background-color: darkgrey;
    padding: 5px;

}

.imgDiv {
    grid-area: img;
}
.buttonDiv {
    grid-area: btn;
    padding-top: 10px;
}
.nameDiv {
    grid-area: name;
}
.statusDiv {
    grid-area: stat;
    text-align: center;
}
.locationDiv {
    grid-area: loc
}
.imgDiv img {
    width: 100px;
    height: 100px;
    border-radius: 50px;
    border: 1px solid red;
}


.buttonDiv, .imgDiv {
    display: flex;
    align-items: center;
    justify-content: center;
}