*,*:after,*:before {
    box-sizing: border-box;
}

.app-wrapper {

    display: grid;
    grid-template-areas: "header header header"
                         "nav content content";
    grid-template-rows: 10vh minmax(89vh,100%);
    grid-template-columns: 2fr 10fr;

    grid-gap: 1vh;
    background-color: #EDEEF0;
}

.content {
    grid-area: content;
    margin-top: 1em;
}

