
.userWrapper {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(400px, 1fr));
    grid-gap: 1em;
    width: 95%;

}

.loadingSpinner img {
    width: 30%;
    height: 30%;
}
.loadingSpinner {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.paginatorWrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 3em;
}