.activePage {
    font-weight: bold;
    color: red;
    padding: 0 2px;
}
.paginator {
    display: flex;
    align-items: flex-end;
}
.paginator img {
    width: 1em;
    height: 1em;
    cursor: pointer;
}
.paginator span {
    font-size: 25px;
    padding: 0 2px;
    cursor: pointer;
}
.paginator span:hover {
    border-top:#192124 2px solid ;
    border-bottom:#192124 2px solid ;
}