.infoWrapper {
    display: grid;
    grid-template-areas: 'img name .'
                         '. text text';
    grid-template-columns: 50px 4fr 4fr;
    padding: 10px;


}
.img {
    grid-area: img;
    width: 50px;
    height: 50px;
}
.img img {
    width: 100%;
    height: 100%;
}
.name {
    grid-area: name;
    font-size: 23px;
    font-weight: bold;
    padding-left: 0.5em;
}
.text {
    grid-area: text;
}