
.wrapper {
    display: grid;
    grid-template-areas: 'img name like'
                         '. text text';
    grid-template-columns: 50px 4fr 4fr;
    padding: 10px;
    background-color: #FFFFFF;
    margin: 1em 0;
    width: 90%;
}
img {
    grid-area: img;
    width: 50px;
    height: 50px;
    border-radius: 30px;
}
.name {
    grid-area: name;
    font-size: 23px;
    font-weight: bold;
    padding-left: 0.5em;
}
.text {
    grid-area: text;
}
.like {
    grid-area: like;
}